@import "utils";
@import "fonts";
@import "typography";
@import "twitter-card";
@import "carousel-button";

body {
  font-family: Lato, sans-serif;
  margin: 0 auto;
  max-width: 500px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.carousel {
  padding: 0;
  display: flex;
  list-style: none;
  width: 310px;
  overflow: hidden;

  &__item {
    position: relative;
  }
}
